/**
 * 协议处理基类，注意入队列的处理
 */
export default abstract class BbwzSocketBaseHandler {
    //判断协议是否需要入队列，默认入队列
    public checkInQueue(msgParam) {
        return true;
    }
    public Handle(msgParam) {
        this.handleData(msgParam);
    }
    //数据逻辑处理
    protected abstract handleData(msgParam);
    //业务逻辑处理
    protected execute(msgParam) { }
    //重连逻辑处理
    protected executeSync(msgParam) {
        this.execute(msgParam);
    }
}